html {
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
  min-width: 320px;
  margin: 0;
  font-family: "Arial", sans-serif;
  font-style: normal;
  background-color: #ffffff;
  list-style-type: none;
  text-decoration: none;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  height: auto;
}

@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.button {
  display: block;
  padding: 10px 44px;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  color: #ffffff;
  background-color: #68b738;
  text-decoration: none;
  text-transform: uppercase;
  border: none;
}

@media (min-width: 768px) {
  .button--large {
    font-size: 20px;
    padding: 13px 26px;
  }
}
.wrapper {
  margin: 0 auto;
  padding: 0 20px;
}
@media (min-width: 768px) {
  .wrapper {
    padding: 0 30px;
  }
}
@media (min-width: 1440px) {
  .wrapper {
    padding: 0 110px;
    max-width: 1440px;
  }
}

.page-header {
  margin: 0 auto;
  padding: 12px 20px 11px 20px;
  width: 100%;
  border-bottom: 1px solid #d9d9d9;
}

@media (min-width: 768px) {
