.main-nav {
  position: relative;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 20px;
  line-height: 20px;
  font-weight: 500;
  text-align: center;
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .main-nav__wrapper {
    padding: 0;
  }
}
.main-nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

@media (min-width: 768px) {
  .main-nav__list {
    margin-right: -17px;
  }
}
.main-nav__toggle {
  position: absolute;
  top: -30px;
  right: -6px;
  z-index: 1;
  width: 35px;
  height: 25px;
  background-color: #ffffff;
  cursor: pointer;
  border: none;
}

@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 1px;
  left: 4px;
  width: 24px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 7px 0 0 #000000, 0 14px 0 0 #000000;
}

@media (max-width: 767px) {
  .main-nav--closed .site-list__item {
    display: none;
  }
}
@media (max-width: 767px) {
  .main-nav--opened .main-nav__wrapper {
    position: absolute;
    top: 11px;
    right: -18px;
    left: -20px;
    z-index: 3;
    padding: 0;
    min-height: 200px;
    background-color: #ffffff;
  }
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}

.main-nav--nojs .main-nav__wrapper {
  position: static;
  padding: 0;
}

.main-nav--nojs .site-list__item {
  display: block;
}

.main-nav--opened .main-nav__toggle {
  top: -34px;
  right: -7px;
}
.main-nav--opened .main-nav__toggle::before, .main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 10px;
  left: 1px;
  width: 30px;
  height: 2px;
  background-color: #000000;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}

