  background-repeat: no-repeat;
  background-position: top center;
  z-index: 0;
}
.promo::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 302px;
  top: 0;
  left: 0;
  background-image: url("../img/background_green.png");
  opacity: 0.9;
}

